@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Poppins:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
body{
    background-color: aqua;
    display: flex;
    justify-content: center;

}



#fundo{

   position: absolute;
   
   top:0px;
   left: 0px;
   height: 100vh;
   width: 100vw;


   background-color:rgba(0, 255, 255, 0.2) ;
   backdrop-filter: blur( 1px );
-webkit-backdrop-filter: blur( 1px );
 
}

#form-criando{
   top: 20%;
   position:absolute;
   width: 500px;
    border: 2px solid black;
    background-color: white;
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

 }
 #form-criando input{width: 80%;}

 #form-criando input[type="text"]{
    height: 40px;
 }

 #form-criando input[type="textarea"]{
    height: 150px;
 }

 #form-criando input[type="button"]{
   margin-bottom: 20px;
 }

#container{
    font-family: 'Lato', sans-serif;
    display: inline-block;
    text-align: center;
    width: 500px;
    height: 600px;
    background-color: white;

    /* display: flex ; */
    /* flex-direction: column;
    justify-content:flex-start; */


}
button{
    font-size:50px ;
    margin-top: 30px;
    color: white;
    background-color: rgb(207, 74, 96);
    border: none;
    height: 60px;
    width: 60px;
}
/* gambiarra muda isso depois*/

 #footer{
    position: absolute;
    bottom: 20px;
 }




.taskelements{
    font-family: 'Lato', sans-serif;
    border: 2px black solid;
    display:flex;
    margin-left: 10px;
    margin-right: 10px;
    height: 67px;
    align-items: center;
    margin-top: 25px;
}

.taskGeneral:hover .buttons{
    display: block;

}


.taskGeneral:hover .descricao{
    display: block;
    margin-left: 10px;
    margin-right: 10px;
    border-top: none;
}

.descricao{
    display: none;
    font-family: 'Lato', sans-serif;
    border: 2px black solid;
}

.buttons{
    
    height: 100%;
    display: none;
   float: right;
    color: white;
    background-color: red;
    margin-right: 0%;
    
}

.title{
    width: 80%;
    text-align: center;
}

.deadline{
   margin-left: 0%;
   padding-left: 0px;
    border-right: 1px solid black;
    width: 20%;
    height:80%;
    display: flex;
    align-items: center;
    justify-content: center;
}

